.container {
  height: 100%;
}
.inner {
  background-color: white;
}
.carryRow {
  display: flex;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}
.carryInput {
  width: 60px;
  height: 40px;
  text-align: center;
  line-height: 40px;
  color: #ffccff;
  border-right: 1px solid rgba(0, 0, 0, 0.1);
  cursor: pointer;
}
.carryInput:last-child {
  border-right: none;
}
.numberRow,
.resultRow {
  display: flex;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}
.redBorder {
  border-bottom: 3px solid var(--p);
}
.yellowBorder {
  border-bottom: 3px solid var(--y);
}
.numberInput,
.resultInput {
  width: 60px;
  height: 60px;
  text-align: center;
  line-height: 60px;
  border-right: 1px solid rgba(0, 0, 0, 0.1);
}
.numberInput h1,
.resultInput h1 {
  font-size: 2.5rem;
  font-weight: 900;
  color: #444444;
  user-select: none;
}
.numberInput:first-child h1 {
  font-size: 2rem;
  color: var(--p);
}
.numberInput:last-child,
.resultInput:last-child {
  border-right: none;
}

.bR1 h1 {
  color: #0070c0;
}
.bR2 h1 {
  color: #00b050;
}
.bR3 h1 {
  color: #7030a0;
}
